html {
    font-size: 62.52%;  
    font-size: 1.5;
}

body {
    color: $color-text;
    font-family: $font_prim;
    font-size: 100%;
    line-height: 1.5;
}

.container {
    max-width: $container-width;
    position: relative;
    margin: 0 auto;
    padding: 0;
}

figure {
    position: relative;
    display: block;
    margin: 0;
    padding: 0;
    font-size: 0;

    img {
        position: relative;
        display: block;
        width: 100%;
        height: auto;
        margin: 0;
        padding: 0;
    }
}

a {
    color: $color-text;

    &:link, &:active, &:visited, &:hover {
        text-decoration: none;
    }

    &:active, &:visited {
        outline: 0;
    }
}
