@font-face {
	font-family: 'icon';
	src:url('../fonts/icomoon.eot?ialru7');
	src:url('../fonts/icomoon.eot?#iefixialru7') format('embedded-opentype'),
		url('../fonts/icomoon.ttf?ialru7') format('truetype'),
		url('../fonts/icomoon.woff?ialru7') format('woff'),
		url('../fonts/icomoon.svg?ialru7#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
}

%iconsetup {
    font-family: 'icon';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"], [class*=" icon-"] {
	@extend %iconsetup;
}

